import { Component, OnInit } from '@angular/core';
import { DbconService } from '../dbcon.service';
import { Router } from '@angular/router';

import Map from 'ol/Map';
import OSM from 'ol/source/OSM';
import OlTileLayer from 'ol/layer/Tile';
import OlView from 'ol/View';
import { fromLonLat } from 'ol/proj';
import  Vector  from 'ol/source/Vector';
import  VectorLayer  from 'ol/layer/Vector';
import { Circle } from 'ol/geom';
import  Feature  from 'ol/Feature';
import { Style,Stroke,Fill, Text } from 'ol/style';


@Component({
  selector: 'app-intro',
  templateUrl: './intro.component.html',
  styleUrls: ['./intro.component.css']
})
export class IntroComponent implements OnInit {

  private info = {name:'Ryu', birthDay:'06.06', Email:'lts06069@naver.com', address:'인천', cat:'assets/cat.PNG'};
  private new_first : any;
  private new_second : any;
  private new_third : any;

  private map: Map;
  private layer: OlTileLayer;
  private view: OlView;

  constructor(private service : DbconService, private rout: Router ) { 
    service.getItem('new_first').subscribe((res)=>{
      this.new_first = res;
    });
    service.getItem('new_second').subscribe((res)=>{
      this.new_second = res;
    });    
    service.getItem('new_third').subscribe((res)=>{
      this.new_third = res;
    });          
  }

  ngOnInit() {

    this.changeColor();  //색깔바꾸깅

    this.layer = new OlTileLayer({
      source: new OSM()
    });

    this.view = new OlView({
      center: fromLonLat([126.697233, 37.481529]),  //내 위치
      zoom: 11
    });
    this.map = new Map({
      target: 'map',
      layers: [this.layer],
      view: this.view
    });  

    var vectorSource = new Vector({projection: 'EPSG:4326'});
    var circle = new Circle([14103606.290725656, 4507770.553246718], 5000);
    var CircleFeature = new Feature(circle);

    vectorSource.addFeatures([CircleFeature]);

    var vectorLayer =new VectorLayer({
      source: vectorSource,
      style: [
      new Style({
          stroke: new Stroke({
              color: 'rgba( 27, 214, 255 ,0.40)',
              width: 2
          }),
          fill: new Fill({
              color: 'rgba( 27, 214, 255 ,0.40)'
          }),
          text : new Text({
            text: '인천 거주!',
            textAlign: 'center',
            font: '20px roboto,sans-serif'            
          })
      })]
    });
    this.map.addLayer(vectorLayer);  
  }

  goHisPage()  : void{
    this.rout.navigateByUrl('history');
  }

  private colorCode : Array<string> = ['rgba(30, 179, 199, 1)','#dd9d22'];
  private useColorCode : string = this.colorCode[0];
  private integer = true;

  changeColor() : void{
    setInterval(()=>{
      if(this.integer){
        this.integer = false;
        this.useColorCode = this.colorCode[1];
      } else {
        this.integer = true;
        this.useColorCode = this.colorCode[0];
      }
    },1500);
  }
}
