module.exports = "/* The actual timeline (the vertical ruler) */\n.timeline {\n    position: relative;\n    max-width: 1500px;\n    margin: 0 auto;\n  }\n/* The actual timeline (the vertical ruler) */\n.timeline::after {\n    content: '';\n    position: absolute;\n    width: 6px;\n    background-color: #5b5b5b;\n    top: 0;\n    bottom: 0;\n    left: 50%;\n    margin: 0px;\n  }\n/* container_cn around content */\n.container_cn {\n    padding: 10px 40px;\n    position: relative;\n    background-color: inherit;\n    width: 50%;\n  }\n/* The circles on the timeline */\n.container_cn::after {\n    content: '';\n    position: absolute;\n    width: 35px;\n    height: 35px;\n    right: -21px;\n    background-color: #5b5b5b;\n    border: 4px solid #464646;\n    top: 15px;\n    border-radius: 50%;\n    z-index: 1;\n  }\n/* Place the container_cn to the left */\n.left {\n    left: 0;\n  }\n/* Place the container_cn to the right */\n.right {\n    left: 50%;\n  }\n/* Add arrows to the left container_cn (pointing right) */\n.left::before {\n    content: \" \";\n    height: 0;\n    position: absolute;\n    top: 22px;\n    width: 0;\n    z-index: 1;\n    right: 30px;\n    border: medium solid #333;\n    border-width: 10px 0 10px 10px;\n    border-color: transparent transparent transparent #333;\n  }\n/* Add arrows to the right container_cn (pointing left) */\n.right::before {\n    content: \" \";\n    height: 0;\n    position: absolute;\n    top: 22px;\n    width: 0;\n    z-index: 1;\n    left: 30px;\n    border: medium solid #333;\n    border-width: 10px 10px 10px 0;\n    border-color: transparent #333 transparent transparent;\n  }\n/* Fix the circle for container_cns on the right side */\n.right::after {\n    left: -16px;\n  }\n/* The actual content */\n.content_ss {\n    padding: 20px 30px;\n    background-color: #333;\n    position: relative;\n    border-radius: 6px;\n  }\n/* Media queries - Responsive timeline on screens less than 600px wide */\n@media screen and (max-width: 600px) {\n  /* Place the timelime to the left */\n    .timeline::after {\n      left: 31px;\n    }\n  \n  /* Full-width container_cns */\n    .container_cn {\n      width: 100%;\n      padding-left: 70px;\n      padding-right: 25px;\n    }\n  \n  /* Make sure that all arrows are pointing leftwards */\n    .container_cn::before {\n      left: 60px;\n      border: medium solid white;\n      border-width: 10px 10px 10px 0;\n      border-color: transparent white transparent transparent;\n    }\n  \n  /* Make sure all circles are at the same spot */\n    .left::after, .right::after {\n      left: 15px;\n    }\n  \n  /* Make all right container_cns behave like the left ones */\n    .right {\n      left: 0%;\n    }\n  }\n/*# sourceMappingURL=data:application/json;base64, */"