import { Component, OnInit } from '@angular/core';
import { DbconService } from '../dbcon.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-history',
  templateUrl: './history.component.html',
  styleUrls: ['./history.component.css']
})
export class HistoryComponent implements OnInit {

  private new_history : any;

  private listss : Array<any> = new Array(); 

  constructor(private service : DbconService, private rout: Router) { 
    this.listss.push({a:1234,b:'abcd'});
  }

  ngOnInit() {
    this.service.getItem('new_history').subscribe((res)=>{
      this.new_history = res;
      this.new_history.map(res=>{
             
        return res;
      })
      console.log(res);
    });
  }

  goback(){
    this.rout.navigateByUrl('intro');
  }
}
