import { Component, OnInit } from '@angular/core';
import {trigger, style, animate, transition,state} from '@angular/animations';
import { Router } from '@angular/router';
import { DbconService } from '../dbcon.service';

@Component({
  selector: 'app-welcome',
  templateUrl: './welcome.component.html',
  styleUrls: ['./welcome.component.css'],
  animations: [
    trigger('fade', [ 
      state('void', style({ opacity: 0 })),
      state('*', style({ opacity: 1 })),
      transition(':enter', animate('1800ms ease-out')),
      transition(':leave', animate('1800ms ease-in')), 
    ])
  ]  
})
export class WelcomeComponent implements OnInit {

  constructor(private rout: Router,private service: DbconService ) {

  }

  ngOnInit() {
    setTimeout(() => {
      var subscript = this.service.getItem('total_access').subscribe((res)=>{
        if(res[0]){
          var mm = Number(res[0].number);
          mm = mm+1;
          this.service.updatItem('total_access',{number:mm},'key');   
          subscript.unsubscribe();
        }
      });      
      this.rout.navigateByUrl('intro');
    }, 2550);
  }

}
